<template>
  <div :class="styling">
      <ul>
        <h3></h3>
        <incomes/>
      </ul>
    <br :class="breaked">
      <ul>
        <h3></h3>
        <expanses/>
      </ul>
  </div>
</template>

<script>
import incomes from "./incomes";
import expanses from "./expenses";
// import { mapState } from "vuex"; //mapGetters
// import { mapGetters } from "vuex"; //mapGetters
export default {
  name: "BudgetList",
  components: {
    incomes,
    expanses
  },
  
  // computed: mapState(['incomes','expenses']),
  computed:{ 
    // ...mapGetters(['income','expense']),
    breaked(){
      var mq = window.matchMedia( "(max-width: 570px)" );
      if (mq.matches) {
          return 'break'
      }
      else {
          return 'br'
      }
    },
    styling(){
        var mq = window.matchMedia( "(max-width: 570px)" );
        if (mq.matches) {
            return 'listFlow'
        }
        else {
            return 'listWrapper'
        }
      },
  },
};
</script>

<style scoped >
.br{
  display: none;
  
}
.break{
  display: block;
}

.listFlow {
  max-width: 1000px;
  margin: 0 auto;
  list-style: none;
  padding: 0;

  ul {
    padding: 0;
  }

  @media (max-width: 600px) {
    grid-template-columns: 1fr;

    ul {
      margin: 0;
      padding: 0 10px;
      display: block;
    }
  }
}

.listWrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 10px;
  max-width: 1000px;
  margin: 0 auto;
  list-style: none;
  padding: 0;

  ul {
    padding: 0;
  }

  @media (max-width: 600px) {
    grid-template-columns: 1fr;

    ul {
      margin: 0;
      padding: 0 10px;
      display: block;
    }
  }
}
h3 {
  margin: 20px;
}
</style>