import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import firebase from "firebase";
// import { v4 as uuidv4 } from 'uuid';

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		status: '',
		token: localStorage.getItem('token') || '',
		loggedin:false,
		dialog: false,
		AddDialog: false,
		records: [],
		incomes:[],
		expenses:[],
		user: localStorage.getItem('user') || {},
	},

	// ---------------------------------------------------------------------------
	// -------------------------- MUTATIONS --------------------------------------
	// ---------------------------------------------------------------------------
	mutations: {
		auth_request(state) {
			state.status = 'loading'
		},
		auth_success(state, token, user) {
			state.user = user
			state.token = token
			state.loggedin = true
			state.status = 'success'
		},
		auth_error(state) {
			state.status = 'error'
		},
		logout(state) {
			state.status = ''
			state.token = ''
			state.loggedin = false
		},
		checked(state, ibool) {
			state.dialog = ibool
		},
		checkAddDialog(state, ibool) {
			state.AddDialog = ibool
		},
		rec(state, inc ) {
			state.incomes = inc
			// state.expenses = exp
			// console.log("DATA");
			// console.log(newRec);
		},
		sec(state, exp) {
			state.expenses = exp
			// console.log("DATA");
			// console.log(newRec);
		}
	},

	// ---------------------------------------------------------------------------
	// ------------------------------ ACTIONS ------------------------------------
	// ---------------------------------------------------------------------------

	actions: {
		login({
			commit
		}, user) {
			console.log(user.email,user.password)
			return new Promise((resolve, reject) => {
				commit('auth_request')
				firebase.auth().signInWithEmailAndPassword(user.email, user.password)
					.then((userCredential) => {
						// Signed in
						var user = userCredential.user
						localStorage.setItem('user', user)
						this.sign = false
						this.dialog = false
						var token = user.uid
						// console.log(`TOKEN ${user.uid}`)
						// console.log(userCredential)
						localStorage.setItem('token', token)
						commit('auth_success', token, user)
						// this.$router.push('/').catch( () => {
						//   this.$router.push('/home')
						// })
						resolve(userCredential)
						// ...
					})
					.catch((error) => {
						// this.alertMesage = error.message;
						commit('auth_error')
						localStorage.removeItem('token')
						reject(error)
					});
			})
		},

		update({
			commit
		}) {
			var db = firebase.firestore();

			firebase.auth().onAuthStateChanged(user => {
				if (user) {
					var refr = db.collection('records')
					var exp = []
					var inc = []

					refr.where('uid', '==', user.uid)
						.onSnapshot(querySnapshot => {

							querySnapshot.forEach((doc) => {
								if (doc.data().type == 'Expense') {
									exp.push(doc.data())
								}
								if (doc.data().type == 'Income') {
									inc.push(doc.data())
								}
								
							});
							// commit('rec', temp)
							commit('sec', exp)
							commit('rec', inc)
						})
					// this.desserts = temp;
				}
			})
		},

		register({
			commit
		}, user) {
			return new Promise((resolve, reject) => {
				commit('auth_request')
				axios({
						url: process.env.R_URL,
						data: user,
						method: 'POST'
					})
					.then(resp => {
						const token = resp.data.token
						const user = resp.data.user
						localStorage.setItem('token', token)
						// Add the following line:
						axios.defaults.headers.common['Authorization'] = token
						commit('auth_success', token, user)
						resolve(resp)
					})
					.catch(err => {
						commit('auth_error', err)
						localStorage.removeItem('token')
						reject(err)
					})
			})
		},

		logout({
			commit
		}) {
			return new Promise((resolve) => {
				commit('logout')
				localStorage.removeItem('token')

				resolve()
			})
		},

		deleteItem({commit},id) {
			var db = firebase.firestore();
			// var refr = db.collection('records')
			
			firebase.auth().onAuthStateChanged(user => {
				if (user) {
					var refr = db.collection('records')

					// console.log(id)
					refr.where('gid', '==', id)
						.onSnapshot(querySnapshot => {
							querySnapshot.forEach((doc) => {
								// console.log(doc.data())
								doc.ref.delete().then(() => {
									console.log("Document successfully deleted!");
								}).catch(function(error) {
									console.error("Error removing document: ", error);
								});
							});

						})
				}
			})
			commit('auth_error')
		}
	},

	// ----------------------------------------------------------------------------
	// ----------------------------- GETTERS --------------------------------------
	// ----------------------------------------------------------------------------

	getters: {
		// isLoggedIn: state => state.loggedin,
		isLoggedIn: state => !!state.token,
		authStatus: state => state.status,
		dialog: state => state.dialog,
		AddDialog: state => state.AddDialog,
		income: state => state.incomes,
		expense: state => state.expenses,
		records: state => state.records,
		getTotalInc({
				incomes
			}) {
				return incomes.reduce(function(ac, item) {
					return ac + parseFloat(item.amount);
				}, 0);
			},
			getTotalExp({
				expenses
			}) {
				return expenses.reduce(function(ac, item) {
					return ac + parseFloat(item.amount);
				}, 0);
			}
	}
})