<template>
 <v-card color='#00520A'>
	<v-card-title color="#fff">
		Incomes <v-spacer></v-spacer> <h5>{{getTotalInc}}</h5>
	</v-card-title>
  <v-simple-table  >
    <template v-slot:default>
      <thead>
        <tr color='#000'>
          <th  color='#000' class="text-left">
            Name
          </th>
          <th class="text-left">
            Date
          </th>
          <th class="text-left">
            Amount
          </th>
          <th class="text-left">
            Delete
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="item in income"
          :key="item.name"
        >
          <td>{{ item.discription }}</td>
          <td>{{ item.date }}</td>
          <td >{{ item.amount }}</td>
          <td>
			<v-btn @click="deleteItem(item.gid)" color='#CC1B00' icon>
				<v-icon>mdi-trash-can</v-icon>
			</v-btn>
          </td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
 </v-card>
</template>


<script>
	import { mapGetters } from 'vuex' // mapMutations,
  export default {
    data () {
      return {
        desserts: [
          {
            name: 'Frozen Yogurt',
            calories: 159,
            date:132
          },
          {
            name: 'Ice cream sandwich',
            calories: 237,
            date:132
          },
          {
            name: 'Eclair',
            calories: 262,
            date:132
          },
          {
            name: 'Cupcake',
            calories: 305,
            date:132
          },

        ],
      }
    },
    methods:{
		deleteItem(id) {
      // console.log('GID: ',id)
      this.$store.dispatch("deleteItem", id).then(async ()=>{
        // this.update()
        const delay = ms => new Promise(res => setTimeout(res, ms));
        await delay(1000);
        this.$router.go()
      }).catch((err) => console.log(err));
    },
    },
    computed:{ 
      ...mapGetters(['income', 'getTotalInc']),
      breaked(){
        var mq = window.matchMedia( "(max-width: 570px)" );
        if (mq.matches) {
            return 'break'
        }
        else {
            return 'br'
        }
      },
      styling(){
          var mq = window.matchMedia( "(max-width: 570px)" );
          if (mq.matches) {
              return 'listFlow'
          }
          else {
              return 'listWrapper'
          }
        },
    },
    async created(){
		// await console.log(this.income)
    },
  }
</script>
<style>
	.green{
		color: green;

	}
</style>