<template>
	<v-dialog
		v-model="AddDialog"
		max-width="700px"
		scrollable
		dark
		>
		<v-card color="#222">
		<v-card-title >
		<span class="headline">New Transaction</span>
		</v-card-title>

		<v-card-text  color="#ffff00">
		<v-container>
		<v-row>
		<v-col
		cols="12"
		sm="6"
		md="4"
		>
		<v-text-field
		v-model="editedItem.discription"
		label="Discription"
		outlined
		></v-text-field>
		</v-col>
		<v-col
		cols="12"
		sm="6"
		md="4"
		>
		<!-- <v-text-field
		outlined
		v-model="editedItem.type"
		label="type"
		></v-text-field> -->
		<v-select
		v-model="editedItem.type"
		outlined
		:items="['Income', 'Expense']"
		label="Record type"
		required ></v-select>
		</v-col>
		<v-col
		cols="12"
		sm="6"
		md="4"
		>
		<v-text-field
		colr="#fff"
		outlined
		v-model="editedItem.date"
		label="date"
		type="date"
		></v-text-field>
		</v-col>

		<v-col
		cols="12"
		sm="6"
		md="4"
		>
		<v-text-field
		outlined
		v-model="editedItem.amount"
		type="number"
		label="amount"
		></v-text-field>
		</v-col>
			</v-row>
			</v-container>
		</v-card-text>

		<v-card-actions>
			<v-spacer></v-spacer>
			<v-btn
			color="red darken-1"
			text
			@click="addDialog"
			>
			Cancel
			</v-btn>
			<v-btn
			color="green"
			text
			@click="save"
			>
			Save
			</v-btn>
		</v-card-actions>
		</v-card>
	</v-dialog>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex'
import firebase from "firebase";
import { v4 as uuidv4 } from 'uuid';

export default {
	data(){
		return{
			editedItem: {
				discription: '',
				type: '',
				date: null,
				amount: null,
			},
		}
	},
	computed:{
		...mapGetters([
			'AddDialog'
		]),
	},
	methods:{
		...mapMutations([
			'checkAddDialog', // map `this.checkAddDialog()` to `this.$store.commit('checkAddDialog')`
		]),
		update(){
			this.$store.dispatch('update').then(()=>{
				console.log("UPDATED")
			})
		},
		addDialog(){
			if (!this.AddDialog) {
			this.$store.commit('checkAddDialog',true)
			}else{

			this.$store.commit('checkAddDialog',false)
			this.editedItem = {
			discription: '',
				type: '',
				date: null,
				amount: null,
			}

			}
		},
		async save(){
			var db = firebase.firestore();

			firebase.auth().onAuthStateChanged(user => {
				if (user) {
					console.log(this.editedItem)
					var refr = db.collection('records')
					const {serverTimestamp} = firebase.firestore.FieldValue
					refr.add({
						uid: user.uid,
						gid:uuidv4(),
						discription: this.editedItem.discription,
						type: this.editedItem.type,
						date: this.editedItem.date,
						amount: this.editedItem.amount,
						createdAt: serverTimestamp(),

					})
					console.log("New Transaction Added!")
				}else{
					console.log("NOT ADDED!")
				}
			})
			this.update()
			const delay = ms => new Promise(res => setTimeout(res, ms))
			await delay(800)
			this.addDialog()
			this.$router.go()
		},
	},
}
</script>
<style>
	.headline{
		color: #ffff00
	}
</style>