<template>
  <v-app>
    <v-app-bar
      app
      color="black"
      dark
    >
      <v-toolbar-title >
        <v-btn plain  to="/dashboard" >
          <h4 >BUDGET MAX</h4>
        </v-btn>
      </v-toolbar-title>


      
      <v-spacer>
      </v-spacer>

        <v-toolbar-title >
          <div v-if="Balance != 0" :class="balClass">
            {{Balance}}
          </div>
      </v-toolbar-title>

      <v-spacer>
      </v-spacer>

      <v-btn @click="addDialog" color="#ffff00" v-if="isLoggedIn" icon>
        <v-icon>mdi-plus</v-icon>
      </v-btn>
      
      <!-- <v-btn @click="logout" color="red" v-if="isLoggedIn" icon>
        <v-icon>mdi-login</v-icon>
      </v-btn> -->

      <v-btn @click="loginDialog" v-if="!isLoggedIn" icon>
        Login
      </v-btn>
    </v-app-bar>

    <v-main>

      <v-row  justify="center">
          <v-dialog
            v-model="dialog"
            persistent
            max-width="600px"
          >
            <!-- <template v-slot:activator="{ on, attrs }">
              
            </template> -->
            <v-card color="grey darken-4">
              <v-card-title>
                <span class="headline">Login</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    
                    
                    <v-col sm="6" cols="12">
                      <v-text-field
                        label="Email"
                        v-model="email"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col sm="6" cols="12">
                      <v-text-field
                        label="Password"
                        type="password"
                        v-model="password"
                        required
                      ></v-text-field>
                    </v-col>

                    <v-col sm="6" cols="12">
                      <v-btn @click="Glogin" color="blue darken-4">
                        <v-icon>mdi-google</v-icon>
                        oogle
                      </v-btn>
                    </v-col>

                    <v-col sm="6" cols="12">
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                          color="red"
                          text
                          @click="loginDialog"
                        >
                          Close
                        </v-btn>
                        <v-btn
                          color="green darken-1"
                          text
                          @click="login"
                        >
                          Login
                        </v-btn>
                      </v-card-actions>
                    </v-col>
                    
                    
                  </v-row>
                </v-container>
              </v-card-text>
              
            </v-card>
          </v-dialog>
        </v-row>
        <addItem/>
      <router-view></router-view>
    </v-main>
  </v-app>
