<script>
// import HelloWorld from './components/HelloWorld';
import { mapMutations, mapGetters } from 'vuex'
import firebase from 'firebase'
import addItem from './components/add.vue'

export default {
  name: 'App',

  components: {
    addItem,
  },

  methods: {
      ...mapMutations([
        'checked', // map `this.increment()` to `this.$store.commit('increment')`
      ]),
      loginDialog(){
        if (!this.dialog) {
          // this.checked(true)
          this.$store.commit('checked',true)
        }else{
          // this.checked(false)
          this.$store.commit('checked',false)

        }
      },
      addDialog(){
        if (!this.AddDialog) {
          // this.checked(true)
          this.$store.commit('checkAddDialog',true)
          // console.log(this.AddDialog)
        }else{
          // this.checked(false)
          this.$store.commit('checkAddDialog',false)
          // this.editedItem = {
          // discription: '',
          // type: '',
          // date: null,
          // amount: null,
        }

      },
      login(){
        let email = this.email 
        let password = this.password
        if (email != '' && password != '') {
          this.$store.dispatch('login', {email, password} )
          .then(() => {
            this.loginDialog()
            // this.$router.push('/dashboard').catch((err) => {
            //   console.error(err)
            // })
          
          })
          .catch(err =>console.log(err.message))
        }
      },
      logout(){
        this.$store.dispatch('logout').then(()=> this.$router.push('/'))
      },
      Glogin(){
        // Body
        const provider = new firebase.auth.GoogleAuthProvider();
        firebase.auth()
          .signInWithPopup(provider)
          .then((result) => {
            /** @type {firebase.auth.OAuthCredential} */
            var credential = result.credential;

            // This gives you a Google Access Token. You can use it to access the Google API.
            var token = credential.accessToken;
            localStorage.setItem('token', token)
            // The signed-in user info.
            var user = result.user;
            console.log(user)
            localStorage.setItem('user', user);
            // this.$router.push('/dashboard').catch((err) => {
            //   console.log(err)
            // })
            
            // ...
          }).catch((error) => {
            // Handle Errors here.
            // var errorCode = error.code;
            // this.alert= true;
            // this.alertMesage = error.message;
            // The email of the user's account used.
            // var email = error.email;
            // The firebase.auth.AuthCredential type that was used.
            // var credential = error.credential;
            console.log(error);
            // ...
          });
      },
    },

  data: () => ({
    //
    email:'',
    password:''

  }),

  created(){
    // console.log(this.Balance);
  },
  computed : {
      isLoggedIn : function(){ 
        // if (this.$store.getters.isLoggedIn) {
        //   this.rooot()
        // }
        console.log(this.Balance)
        return this.$store.getters.isLoggedIn
      },
      balClass(){
        if (this.Balance > 0) {
          return 'Balance'
        }else{
          return 'negativeBalance'
        }
      },
      Balance() {
        var sum = this.getTotalInc - this.getTotalExp
        // console.log('SUM = ',sum)
        return sum;
      },
      // dialog: function () {
      //   // body...
      //   return this.$store.state.dialog;
      // },...mapGetters(["getTotalInc", "getTotalExp"])
      ...mapGetters([
            'dialog',
            'AddDialog',
            'getTotalInc',
            'getTotalExp'
            // ...
          ])
    },
};
</script>
<style>
  h4{
    color: #ffff00;
  }
  .Balance{
